.advantages-section {
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    /*background-position: bottom;*/
    position: relative;
    /*height: auto;*/
    height: 100vh;
    background-image: url("../img/toyBg.jpeg");
}

.advantages-card {
    background: #e5dec1cc;
    border-radius: .5em;
    box-shadow: 0 0 10px 0 #565656;
    position: absolute;
    top: 20%;
    left: 46%;
    margin: 2em;
    padding: 1em 2em;
}

.introduction-header span {
    /*background-color: #114d4d;*/
}

.about-header {

}

.about-header-text {
    font-family: 'Ubuntu', Arial, sans-serif;
    font-size: 1.5em;
    color: #565656;
}

.about-us {
    text-decoration: none;

}

.about-us-point {
    list-style: none;
    text-decoration: none;
    padding: .3em;
    display: flex;
    align-items: center;
    margin: 0 .5em;
    box-sizing: border-box;
}

.check {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    background: #edb941;
    border: 1px solid #755612;
    border-radius: 50%;
    margin: .3em;
    color: white;
    box-shadow: 0 0 10px 0 #e2b249;

}

.check-text {
    max-width: 90%;
    font-size: 1.1em;
}

@media (max-width: 1100px) {
    .advantages-card {
        left: 30%;
    }
}

@media (max-width: 850px) {
    .advantages-card {
        left: 0;
    }
}

@media (max-width: 600px) {
    .advantages-card {
        top: 10%;
    }
}

@media (max-width: 480px) {
    .check-text{
        font-size: .9em;
    }
    .about-us{
        padding: 0;
        margin: 0;
    }
    .advantages-card {
        top: 0;
        margin: .5em;
        padding: 1em .5em;
    }
}

@media (max-width: 400px) {
    .about-us-point{
        margin: 0;
        padding: .2em;
    }
    .check {
        margin: 0 .3em 0 0;
        min-width: 25px;
    }
}
@media (max-width: 350px) {
    .check-text{
        font-size: .8em;
    }
    .about-header-text {
        font-size: 1.2em;
    }
}
