.header {
    width: 100%;
    height: 100px;
    background: #9a8f7199;
    position: absolute;
    top: 0;
    z-index: 500;
    background: linear-gradient(to right, #35675ccc, #638e7bcc, #2d5e5bcc);
}
.special-offer{
    position: fixed;
    top: 0;
    width: 100%;
    background: #0a3131;
    z-index: 600;
    color: #d2c8c8;
    text-align: center;
}
.special-offer a{
    color: #e2b249;
}

.header a {
    text-decoration: none;
    color: inherit;
}

.mobile-header a {
    text-decoration: none;
    color: inherit;
}

.header-wrapper {
    display: flex;
    justify-content: space-around;
    height: 100%;
}

.header-item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: flex-end;
    height: 100%;
    text-align: center;
    font-size: 1.45em;
    color: #d2c8c8;
    cursor: pointer;
}

.header-item span:after {
    content: "";
    display: block;
    height: 2px;
    background-color: #d2c8c8;
    width: calc(100% + 8px);
    left: -4px;
    position: absolute;
}

.header-item span .underline {
    content: "";
    display: block;
    height: 2px;
    background-color: #d2c8c8;
    width: 0;
    left: -4px;
    position: absolute;
    transition: width .5s ease;
}

.header-item:hover span:after,
.header-item.active span:after {
    display: none;
}

.header-item:hover,
.header-item.active {
    color: #eed690;
}

.header-item:hover span .underline,
.header-item.active span .underline {
    content: "";
    display: block;
    height: 2px;
    background-color: #eed690;
    width: calc(100% + 8px);
    left: -4px;
    position: absolute;
}

.mobile-header {
    display: none;
    background: linear-gradient(to right, #35675ccc, #638e7bcc, #2d5e5bcc);
    position: absolute;
    height: 100vh;
    z-index: 500;
    width: 100vw;
    left: 100vw;
    transition: all .5s ease;
    top: 0;
}

.mobile-header {
    display: none;
    flex-direction: column;
    height: 100%;
    position: fixed;
}
.mobile-header-wrapper{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.mobile-header.active {
    left: 0;
}


.mobile-header a {
    display: flex;
    justify-content: center;
}

.mobile-header a > span {
    position: relative;
}

.mobile-header-item {
    flex: 1 1;
}

.burger {
    z-index: 1000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 2em;
    height: 1.5em;
    position: fixed;
    top: 0;
    right: 0;
    margin: 2.5em 1em 1em 1em;
}

.line {
    background: white;
    width: 2em;
    height: 2px;
    transition: all .5s ease;
    border: 1px solid #777;
}

.mobile-header-toggle.active .first-line {
    transform: rotate(45deg);
    margin-top: .75em;
    position: absolute;
}

.mobile-header-toggle.active .second-line {
    display: none;
}

.mobile-header-toggle.active .third-line {
    transform: rotate(-45deg);
    margin-top: .75em;
    position: absolute;
}

